.media-box {
	padding: 100px 0;
	@include laptop {
		padding: 60px 0; }
	@include tablet {
		padding: 40px 0; }
	@include xs {
		padding: 10px 0;
		margin-bottom: 44px; }
	//+md
 //     display: none
	&__grid {
		padding-top: 39.7%;
		width: 100%;
		position: relative;
		@include md {
			padding-top: 52.25%; }
		@include sm {
			padding-top: 264.75%; }
		& + .section__text {
			margin-top: 57px;
			@include tablet {
				margin-top: 30px; } } }

	&__text {
		@include xs {
			padding: 0 20px; } } }
.m-grid {
	@include coverdiv;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
	grid-template-rows: 1fr 1fr 1fr;
	grid-template-areas: "m-grid__1 m-grid__1 m-grid__2 m-grid__3 m-grid__4" "m-grid__1 m-grid__1 m-grid__5 m-grid__5 m-grid__4" "m-grid__6 m-grid__6 m-grid__5 m-grid__5 m-grid__7";
	@include sm {
		grid-template-columns: 1fr 1fr;
		grid-template-rows: 1fr 1fr 1fr 1fr;
		grid-template-areas: "m-grid__1 m-grid__1" "m-grid__2 m-grid__3" "m-grid__6 m-grid__6" "m-grid__7 m-grid__7"; }
	&__item {
		position: relative;
		&:after {
			position: absolute;
			content: '';
			display: block;
			width: 100%;
			height: 100%;
			background-color: #000000;
			opacity: 0;
			top: 0;
			left: 0;
			pointer-events: none;
			transition: opacity .3s ease; }
		@include hover {
			&:after {
				opacity: .5; } } }
	img {
		height: 100%;
		width: 100%;
		object-fit: cover; } }

.m-grid__1 {
	-ms-grid-column: 1;
	-ms-grid-column-span: 2;
	-ms-grid-row: 1;
	-ms-grid-row-span: 2;
	grid-area: m-grid__1;
	@include sm {
		-ms-grid-column: 1;
		-ms-grid-column-span: 2;
		-ms-grid-row: 1;
		-ms-grid-row-span: 2; } }


.m-grid__2 {
	-ms-grid-column: 3;
	-ms-grid-column-span: 1;
	-ms-grid-row: 1;
	grid-area: m-grid__2; }

.m-grid__3 {
	-ms-grid-column: 4;
	-ms-grid-column-span: 1;
	-ms-grid-row: 1;
	grid-area: m-grid__3; }

.m-grid__4 {
	-ms-grid-column: 5;
	-ms-grid-column-span: 4;
	-ms-grid-row: 1;
	-ms-grid-row-span: 2;
	grid-area: m-grid__4;
	@include sm {
		display: none; } }

.m-grid__5 {
	-ms-grid-column: 3;
	-ms-grid-column-span: 2;
	-ms-grid-row: 2;
	-ms-grid-row-span: 2;
	grid-area: m-grid__5;
	@include sm {
		display: none; } }

.m-grid__6 {
	-ms-grid-column: 1;
	-ms-grid-column-span: 2;
	-ms-grid-row: 3;
	grid-area: m-grid__6; }


.m-grid__7 {
	-ms-grid-column: 5;
	-ms-grid-column-span: 1;
	-ms-grid-row: 3;
	grid-area: m-grid__7; }
