.speakers {
	padding: 145px 0 17px;
	@include sm {
		padding-top: 53px; }
	&_pt-sm {
		padding-top: 94px;
		@include laptop {
			padding: 80px 0 10px; } }
	&_center {
		.speakers-list {
			justify-content: center;
			@include xs {
				justify-content: flex-start;
				//flex-direction: column
 } } }				//align-items: center
	@include laptop {
		padding: 80px 0 10px; }
	//&_mb-sm
	//speakers-list
	//&__item
	//	&:last-child
	//		@include xs
	//			margin-bottom: 30px
	&__btn {
		max-width: 283px;
		margin: 0 auto; } }

.speakers-list {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -19px;
	&__item {
		width: 20%;
		padding: 0 19px;
		margin-bottom: 80px;
		@include r(1440) {
			width: 25%; }
		@include tablet {
			width: 25%;
			margin-bottom: 48px; }
		@include md {
			width: 33.33%;
 }			//margin-bottom: 30px
		@include sm {
			width: 50%;
 }			//margin-bottom: 30px
		@include xs {
			padding: 0 10px;
			//width: 100%
			//max-width: 250px
 } } }			//margin: 0 auto 48px
.speaker {
	&__photo {
		width: 100%;
		padding-top: 100%;
		position: relative;
		margin-bottom: 35px;
		@include xs {
			margin-bottom: 23px; }
		img {
			@include coverdiv;
			object-fit: cover; } }
	&__name {
		font-size: 26px;
		@include bold;
		margin-bottom: 11px;
		letter-spacing: 0.65px;
		overflow: hidden;
		text-overflow: ellipsis; }
	&__company {
		margin-bottom: 30px;
		@include xs {
			font-size: 14px;
			margin-bottom: 20px; } }
	&__desc {
		color: #888;
		font-size: 13px;
		line-height: 2;
		letter-spacing: 1.1px;
		word-wrap: break-word;
		@include sm {
			color: #aaaaaa;
			font-size: 13px; }
		@include xs {
			font-size: 12px;
			margin-bottom: 21px; }
		a {
			color: #bdbdbd;
			text-decoration: none;
			&:hover {
				text-decoration: underline; } }
		&:not(:last-child) {
			margin-bottom: 22px;
			@include xs {
				margin-bottom: 21px; } } }
	&__socials {
		display: flex; }
	&__social-link {
		&:not(:last-child) {
			margin-right: 16px; }
		.icon {
			transition: 0.3s;
			font-size: 18px;
			fill: #aaaaaa; }
		.no-touch &:hover {
			.icon {
				fill: $themeColor; } } }
	&--mc {
		.speaker {
			&__photo {
				width: 179px;
				padding-top: 179px;
				margin: 0 auto 59px;
				&:after {
					pointer-events: none;
					content: '';
					position: absolute;
					top: 49%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 235px;
					height: 235px;
					background-size: contain;
					background-position: center;
					background-repeat: no-repeat;
					background-image: url(../img/mc-decor.png); } }
			&__photo-wrap {
				border-radius: 50%;
				@include coverdiv;
				overflow: hidden; } } } }


.mc {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	padding: 102px 0 122px;
	@include xs {
		padding: 37px 0 55px;
 } }		//margin-top: 20px

.mc-list {
	display: flex;
	justify-content: center;
	margin: 0 auto;
	@include xs {
		padding-top: 20px;
		display: block; }
	&__item {
		max-width: 225px;
		@include xs {
			margin: 0 auto; }
		&:not(:last-child) {
			margin-right: 45px;
			@include sm {
				margin-right: auto;
				margin-bottom: 79px; } } } }


